<script>
  import axios from 'axios'

  export default {
    data() {
      return {
        isCollapse: false
      }
    },
    mounted(){
      this.checkLogin();
    },
    components:{

    },
    methods:{
      checkLogin(){
        axios.post('/admins/checkLogin').then((response)=>{
          let res = response.data;
          if(res.status == '0'){
            this.$store.commit('updateAdminInfo',res.result)
          }else{
            this.$router.push('/admin')
          }
        })
      },




    },


  }
</script>



